.dialogs {
    display: grid;
    grid-template-areas: 'd m';
    grid-template-columns: 200px 1fr;
    background-color: white;
    grid-gap: 3px;
    min-height: calc(100vh - 83px);
}

.dialog {
    grid-area: d;
    background-color: gold;
    height: 100%;
    width: 200px;
    position: fixed;
    transition: 0.5s
}

.dialog .activeName {
    transform: scale(1.5,1.5);
    margin-left: 5vw;
    transition: 0.5s;
}

.messages {
    grid-area: m;
    background-color: gold;
    padding: 10px;
    align-self: start;
    min-height: calc(100vh - 103px);
}

.messages div {
    padding: 7px;
}

.messages .sendArea {
    position: fixed;
    bottom: 20px;
    text-align: center;
}

.messages .sendArea textarea {
    width: 50vw;
    resize: none;
}

.messages .choosingDialog {
    font-size: 2em;
    padding: 50px;
    font-style: italic;
    color: gray;
}

.message {
    color: saddlebrown;
    text-align: start;
}